body.home  {
	.entry-content {
		padding: 0;
	}
	.site-main {
		padding-bottom: 0;
	}
	.logo-header {
		opacity: 0;
		transition:opacity ease-in-out 0.4s;
		&.fade-in {
			opacity: 1;
		}
	}
}

.home-gris {
	background: transparent linear-gradient(180deg, #fff 0%, #F7F7F7 100%) 0% 0% no-repeat padding-box;
	font-weight: 700;
	padding:4rem 2rem;

	h2 {
		--couleur:var(--wp--preset--color--noir);
		@include titre-section-avec-trait;
	}
	p {
		margin-bottom: 1em;
	}
	strong {
		color:var(--wp--preset--color--noir);
	}

	--picto-w:2.3rem;
	ul {
		list-style: none !important;
	}
	li {
		margin-bottom: 1em;
		padding-left: calc(1.5 *  var(--picto-w));
		position: relative;

		&::before {
			background-image: url('icons/check-cyan.svg');
			background-repeat: no-repeat;
			background-size: contain;
			content:'';
			display: block;
			height:var(--picto-w);
			left:0;
			position:absolute;
			top:5px;
			width:var(--picto-w);
		}
	}
	
	@include bp(md) {
		p, ul {
			margin-left:auto;
			margin-right: auto;
			max-width: 80%;
		}
	}

	@include bp(lg) {
		padding-top: 13.5rem;
		padding-bottom: 10rem;
		p, ul {
			max-width: 100rem;
		}
		p {
			font-size: 3rem;
			line-height: 4rem;
		}
		ul {
			font-weight: 600;
			font-size: 2rem;
			line-height: 2.8rem;
		}
	}
}

.home-solutions {
	background-image: url('icons/dots-degrade.svg');
	background-repeat: repeat-x;
	padding:10rem 2rem 4rem;


	h2 {
		--couleur:var(--wp--preset--color--noir);
		@include titre-section-avec-trait;
	}

	img {
		margin: 0 auto 2rem;
		max-width:12rem;
	}
	.wp-block-column {
		margin-bottom: 4rem;
	}
	.wp-block-column:first-of-type {
		border-bottom:solid 1px #EBEBEB;
		padding-bottom: 4rem;
	}

	& > p.has-text-color {
		font-size: 150%;
		margin-bottom: 2rem;
	}

	p + ul {
		margin-top: -0.5em;
	}
	
	
	@include bp(md) {
		padding-bottom: 6rem;
		--gap:4rem;
		h2 {
			margin-bottom: 6rem;
		}
		.wp-block-column {
			margin-bottom: 6rem;
		}
		.wp-block-column:first-of-type {
			border-bottom:none;
			border-right:solid 1px #EBEBEB;
			padding-bottom: 0;
			padding-right:var(--gap);
		}
		.wp-block-column:nth-of-type(2) {
			padding-left: var(--gap);
		}
	}

	@include bp(lg) {
		--gap:8rem;
		padding: 14rem 3vw 10rem;

		img {
			margin-bottom: 4rem;
			max-width:30rem;
		}

		.wp-block-column {
			margin-bottom: 8rem;
		}
	}

	@include bp(xl) {
		--gap:13.5rem;
		margin: 0 auto;
		padding-left: 0;
		padding-right: 0;
		max-width: 124.4rem;

		img {
			margin-bottom: 5rem;
		}
	}
}

.home-advisory {
	background: transparent linear-gradient(180deg, #FFFFFF 0%, #F7F7F7 100%) 0% 0% no-repeat padding-box;
	ul {
		font-weight: 400;
		li strong {
			color:var(--wp--preset--color--cyan-ad);
			font-weight: 700;
		}
	}
	@include bp(lg) {
		padding-top: 8rem;
		p {
			font-size: 2.4rem;
			line-height: 3.2rem;
		}
	}
}

.home-academy {
	padding:8rem 2rem 4rem;
	position:relative;

	&::after {
		background-image: url('icons/dots-degrade.svg');
	background-repeat: repeat-x;
		content:'';
		height:100%;
		left:0;
		position:absolute;
		top:0;
		width: 100%;
	}

	&::before {
		//NOTE si besoin de la modifier depuis le BO utiliser enable media replace
		background-image: url('/wp-content/uploads/2024/06/img-bkg-academy.jpg');
		background-size: cover;
		background-position: center center;
		background-repeat: no-repeat;
		content:'';
		height:100%;
		left:0;
		position:absolute;
		top:0;
		width: 100%;
	}
	& > * {
		position: relative;
	}

	h2 {
		--couleur:var(--wp--preset--color--noir);
		@include titre-section-avec-trait;
	}

	p:first-of-type {
		font-weight: 700;
	}

	@include bp(md) {
		padding-bottom: 6rem;

		p, ul {
			margin-left:auto;
			margin-right: auto;
			max-width: 80%;
		}
	}
	@include bp(lg) {
		padding-bottom: 11rem;
		p, ul {
			max-width: 100rem;
		}
		p:first-of-type {
			font-size: 2.4rem;
			line-height: 3.2rem;
		}
	}
}


